















import {Component, Vue} from "vue-property-decorator"
import PageBackground from "@/components/PageBackground.vue"
import Portal from "@/managers/portal"
import i18n from "../i18n"

@Component({ components: { PageBackground } })
export default class TheWelcome extends Vue {
	public carouselItems: object[] = [
		{
			title: "welcome.title1",
			text: "welcome.text1"
		}, {
			title: "welcome.title2",
			text: "welcome.text2"
		}, {
			title: "welcome.title3",
			text: "welcome.text3"
		}
	]
	private isSubscribed: string | null = null
	private temp: string | null = null
	private created(): void {
		this.temp = window.localStorage.getItem("name")
		this.isSubscribed = window.localStorage.getItem("isSubscribed")
		window.localStorage.removeItem("isSubscribed")
	}
	private openNext(): void {
		this.$router.push("/home")
		}
		}
